/*
 * 后台框架样式变量
 * -------------------------
 */

/* 顶部工具栏高度 */
$header-height: 50px;

$mini-width: 65px;

/* 页面左边的宽度 */
$left-width: 19%;

/* 页面右边的宽度 */
$right-width: 81%;

/* 页面底部的宽度 */
$footer-height: 42px;

/* 整个页面的可视范围的高度 */
$max-vh: 100vh;
$max-vw: 100vw;

/* tabs页面标签栏的高度 */
$tab-header-height: 42px;

/* tabs页面标签栏按钮的宽度 */
$tab-header-btn-width: 40px;
$grid-breakpoints: (
  xs: 0,
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px,
  xxl: 1400px
) !default;
$form-check-input-margin-x: .25rem !default;
