.xq-table{
  display: table;
  width: 100%;
  margin-bottom: 0;
  color: #212529;
  vertical-align: top;
  border-color: #dee2e6;
  border-collapse: collapse;
  >.thead{
    display: table-header-group;
    vertical-align: bottom;
  }
  >.tbody{
    display: table-row-group;
    vertical-align: inherit;
    .td{
      padding: .3rem;
    }
  }
  >.tfoot{
    display: table-footer-group;
    vertical-align: bottom;
  }
  .tr{
    display: table-row;
  }
  .td{
    display: table-cell;
    vertical-align: middle;
    .form-switch{
      width: 32px;
      margin: auto;
    }
  }
  .cols{
    display: table-caption;
  }
  .tbody,
  .td,
  .tfoot,
  .thead,
  .tr {
    border-color: inherit;
    border-style: solid;
    border-width: 0;
  }
  > :not(.caption) > div > div {
    padding: .5rem;
    background-color: transparent;
    border-bottom-width: 1px;
    box-shadow: inset 0 0 0 9999px transparent;
  }
  > :not(:first-child),
  .tfoot {
    border-top: (2 * 1px) solid currentColor;
  }
}
.xq-table-bordered {
  > :not(.caption) > .tr {
    border-width: 1px 0;
    > .td {
      border-width: 0 1px;
    }
  }
}
.xq-table-borderless {
  // stylelint-disable-next-line selector-max-universal
  > :not(caption) > div > div {
    border-bottom-width: 0;
  }
  > :not(:first-child) {
    border-top-width: 0;
  }
}
.xq-table-striped {
  > .tbody > .tr:nth-of-type(odd) > div {
    color: #212529;
    background-color: rgba(0, 0, 0, .05);
  }
}
.xq-table-hover {
  > .tbody > .tr:hover > div {
    color: #212529;
    background-color: rgba(0, 0, 0, .05);
  }
}
