/**
* 行内表单
*/
.form-inline {
  display: flex;
  flex-flow: row wrap;
  align-items: center;

  .form-check {
    width: 100%;
  }

  @include media-breakpoint-up(sm) {
    label,
    .form-group,
    .form-check{
      display: flex;
    }
    label,
    .form-group{
      margin-bottom: 0;
    }
    label {
      align-items: center;
      justify-content: center;
    }
    .form-group,
    .form-check {
      align-items: center;
    }

    .form-group {
      flex: 0 0 auto;
      flex-flow: row wrap;
    }

    .form-control,
    .form-control-plaintext {
      display: inline-block;
    }

    .form-control {
      width: auto;
      vertical-align: middle;
    }

    .input-group,
    .custom-select {
      width: auto;
    }
    .form-check {
      display: flex;
      justify-content: center;
      width: auto;
      padding-left: 0;
    }
    .form-check-input {
      position: relative;
      flex-shrink: 0;
      margin-top: 0;
      margin-right: $form-check-input-margin-x;
      margin-left: 0;
    }

    .custom-control {
      align-items: center;
      justify-content: center;
    }
    .custom-control-label {
      margin-bottom: 0;
    }
  }
}
