.xq-treegrid-indent {
  width: 10px;
  height: 10px;
  margin-top: 15px;
  float: left;
  display: inline-block;
  position: relative;
  background-image: url("/assets/images/indent.gif");
  background-position: -3px;
}

.xq-treegrid-expander {
  width: 10px;
  height: 10px;
  margin-top: 15px;
  float: left;
  display: inline-block;
  position: relative;
  cursor: pointer;
  background-image: url("/assets/images/indent.gif");
  background-position: -3px;
}
td span.xq-treegrid-indent:first-child{
  background-position: 0;
  margin-left: 4px;
}
.xq-treegrid-expander-expanded{
  background-image: url("/assets/images/collapse.png");
}
.xq-treegrid-expander-collapsed{
  background-image: url("/assets/images/expand.png");
}
.xq-treegrid .xq-edit{
  margin-right: 3px;
}

.xq-treegrid .xq-delete{
  margin-right: 3px;
}


.xq-treegrid.xq-table-striped {
  > tbody:nth-of-type(odd) > tr > td {
    color: #212529;
    background-color: rgba(0, 0, 0, .05) !important;
  }
}
.xq-treegrid.xq-table-hover {
  > tbody > tr:hover > td {
    color: #212529;
    background-color: rgba(0, 0, 0, .05);
  }
}
.xq-treegrid > :not(:first-child) {
  border-top: none;
}
.xq-treegrid> thead{
  border-bottom: 2px solid currentColor;
}
.xq-treegrid > tfoot{
  border-top: 2px solid currentColor !important;
}