#xqkeji-page-body
{
  margin-top: .2rem;
  .table tbody tr td
  {
    padding: .3rem;
  }
  td{
    text-align: center;
    vertical-align: middle;
    .form-switch{
      width: 32px;
      margin: auto;
    }
  }
  th{
    text-align: center;
    vertical-align: middle;
  }
  >.card-header
  {
    color: #fff;
  }
}
#xqkeji-form
{
  margin-top: .2rem;
  .card-header{
    color: #fff;
    height: 3rem;
  }
  .form-group {
    margin-bottom: 1rem;
  }
  #xqkeji-form-body
  {
    margin-top: .5rem;
  }
  .xqkeji-check-group{
    list-style: none;
    padding: 0;
    margin: 0;
    li{
      float: left;
      padding: 0 10px 0 0;
      label{
        margin-left: .5rem;
      }
    }
  }
  .xq-auth-table{
    td{
      vertical-align: middle;
    }
    .xq-auth-check-table{
      height: 3rem;
    }
    .xq-auth-check-row{
      min-width: 10rem;
      min-height: 4rem;
    }
  }
  .xqkeji-auth-check {
    list-style: none;
    padding: 0;
    li{
      float: left;
      padding: 0 10px;
      line-height: 2rem;
      display: flex !important;
      align-items: center !important;
      label{
        margin-left: .2rem;
      }
    }
  }
  .xq-tab-form{
    .tab-content{
      padding-top: .5rem;
    }
  }
}
.xq-captcha{
  cursor: pointer;
}
.card-header.bg-success{
  color: #fff;
}
.card-header{
  height: 50px;
  max-height: 50px;
  .form-inline{
    margin-top: -2px;
  }
}
.file-drop-zone{
  min-height: auto !important;
  .file-drop-zone-title
  {
    padding: 45px 10px;
  }
}
.container-fluid > .row > div > h2 > b {
  color: #fff;
}
.xq-move{
  cursor: move;
}
.xq-dragger-table.xq-table-striped {
  > tbody:nth-of-type(odd) > tr > td {
    color: #212529;
    background-color: rgba(0, 0, 0, .05) !important;
  }
}
.xq-dragger-table.xq-table-hover {
  > tbody > tr:hover > td {
    color: #212529;
    background-color: rgba(0, 0, 0, .05);
  }
}
.xq-dragger-table > :not(:first-child) {
  border-top: none;
}
.xq-dragger-table > thead{
  border-bottom: 2px solid currentColor;
}
.xq-dragger-table > tfoot{
  border-top: 2px solid currentColor !important;
}
